import axios from "axios";
import store from "@/store";

//  axios 
let instance = axios.create({
  // baseURL: "http://localhost:3000",
  // baseURL: "http://192.168.31.43:3000",
  baseURL: "https://musicapi.yjsjyb.top",
  timeout: 1000 * 8,
  // ,  cookie https://github.com/Binaryify/NeteaseCloudMusicApi/issues/1257
  withCredentials: true,
});

// 
instance.interceptors.request.use(
  (config) => {
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

// 
instance.interceptors.response.use(
  (response) => {
    if (response.status === 200) {
      return Promise.resolve(response);
    } else {
      return Promise.reject(response);
    }
  },
  (error) => {
    if (error.response) {
      console.log(" error.response:", error.response);
      return Promise.reject(error.response);
    } else if (error.request) {
      console.log(" error.request:", error.request);
      return Promise.reject(error.request);
    } else {
      console.log(" error.message:", error.message);
      return Promise.reject(error.message);
    }
  }
);

/**
 * get
 * @param {String} url [url]
 * @param {Object} params []
 */
export function get(url, params = {}) {
  return new Promise((resolve, reject) => {
    //  cookie
    if (store.getters.loginStatus) {
      Object.assign(params, { cookie: store.getters.loginCookie });
    }
    // console.log('get ', url);
    instance
      .get(url, {
        params: { timestamp: Date.now(), ...params },
      })
      .then((res) => {
        //  data
        resolve(res.data);
      })
      .catch((err) => {
        console.error("get , :", url, "\n", err);
        reject(err);
      });
  });
}

/**
 * post
 * @param {String} url [url]
 * @param {Object} params []
 */
export function post(url, params = {}) {
  return new Promise((resolve, reject) => {
    //  cookie
    if (store.getters.loginStatus) {
      Object.assign(params, { cookie: store.getters.loginCookie });
    }
    // console.log('post ', url)
    // POST url , 
    instance
      .post(`${url}?timestamp=${Date.now()}`, params)
      // .post(`${url}`, params)
      .then((res) => {
        //  data
        resolve(res.data);
      })
      .catch((err) => {
        console.error("post , :", url, "\n", err);
        reject(err);
      });
  });
}
